
'use strict'
import { Card } from './src/components/Card/Card'
import { ModalWindow } from './src/components/modal-window/modal-window'
import { ModalProgres } from './src/components/modal-progress/modal-progress'
import { ModalProgresAll } from './src/components/modal-progress-allCards/modal-progress-allCards'
import { Transfer } from './src/components/transfer/Transfer'
const addBtn = document.querySelector('#add')
addBtn.addEventListener('click', () => {
  const card = new Card()
  card.render()
})
const modalWindowComponent = new ModalWindow()
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('card__left-part') || event.target.parentNode.classList.contains('card__left-part')) {
    if (event.target.parentNode.classList.contains('card__left-part')) {
      const card = event.target.parentNode
      modalWindowComponent.card = card
      modalWindowComponent.render()
    } else {
      const card = event.target
      modalWindowComponent.card = card
      modalWindowComponent.render()
    }
  }
})

// modalProgress.template()
const modalProgress = new ModalProgres()
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('delite-card')) {
    modalProgress.render()
  }
})
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('close-modal') || event.target.classList.contains('close') || event.target.classList.contains('delete')) {
    modalProgress.template.remove()
  }
})
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('delete')) {
    const title = document.querySelector('.card')
    title.parentNode.removeChild(title)
  }
})
// modalProgressAll.template()
const modalProgressAll = new ModalProgresAll()
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('delt')) {
    modalProgressAll.render()
  }
})
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('close-modal') || event.target.classList.contains('close') || event.target.classList.contains('deleteAll')) {
    modalProgressAll.template.remove()
  }
})
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('deleteAll')) {
    Element.prototype.remove = function () {
      this.parentElement.removeChild(this)
    }
    NodeList.prototype.remove = HTMLCollection.prototype.remove = function () {
      for (let i = this.length - 1; i >= 0; i--) {
        if (this[i] && this[i].parentElement) {
          this[i].parentElement.removeChild(this[i])
        }
      }
    }
    document.getElementsByClassName('card').remove()
  }
})
// Complete card cleaning ("Done")

document.addEventListener('click', (event) => {
  if (event.target.classList.contains('del')) {
    Element.prototype.remove = function () {
      this.parentElement.removeChild(this)
    }
    NodeList.prototype.remove = HTMLCollection.prototype.remove = function () {
      for (let i = this.length - 1; i >= 0; i--) {
        if (this[i] && this[i].parentElement) {
          this[i].parentElement.removeChild(this[i])
        }
      }
    }
    document.getElementsByClassName('card').remove()
  }
})

// перенос карты
const transfer = new Transfer()
document.addEventListener('click', (event) => {
  if (event.target.classList.contains('transfer-card')) {
    transfer.renderNext()
  } else if (event.target.classList.contains('back-card')) {
    transfer.renderBack()
  }
})
