'use strict'
export function ModalWindow () {
  const createModalBlock = function (title, description, userAvatar, userName, Data) {
    const modalBack = document.createElement('div')
    modalBack.className = 'modal_back'

    const modalWindow = document.createElement('div')
    modalWindow.className = 'modal_window'

    const modalWindowData = document.createElement('div')
    modalWindowData.className = 'modal_window__data'
    // автар и пользователь
    const modalWindowDataUser = document.createElement('div')
    modalWindowDataUser.className = 'modal_window__data__user'

    const modalWindowDataUserAvatar = document.createElement('div')
    modalWindowDataUserAvatar.className = 'modal_window__data__user-avatar'
    modalWindowDataUserAvatar.innerHTML = userAvatar.textContent

    const modalWindowDataUserName = document.createElement('div')
    modalWindowDataUserName.className = 'modal_window__data__user-name'
    modalWindowDataUserName.innerHTML = userName.textContent
    //
    const modalWindowDataTxtName = document.createElement('div')
    modalWindowDataTxtName.className = 'window__data__txt-name'
    modalWindowDataTxtName.textContent = 'Название карточки '
    // //////////////
    const modalWindowDataName = document.createElement('textarea')
    modalWindowDataName.className = 'window__data__name'
    modalWindowDataName.textContent = title.textContent

    const modalWindowDataDescriptionTxt = document.createElement('div')
    modalWindowDataDescriptionTxt.className = 'window__data__description-txt'
    modalWindowDataDescriptionTxt.textContent = 'Описание'

    const modalWindowDataDescriptionDiv = document.createElement('div')
    modalWindowDataDescriptionDiv.className = 'window__data__description'

    const textArea = document.createElement('textarea')
    textArea.className = 'description'
    textArea.placeholder = 'Добавьте более подробное описание...'
    textArea.textContent = description.textContent

    const button = document.createElement('button')
    button.className = 'window__data__save-description'
    button.textContent = 'Сохранить'

    const modalWindowIcon = document.createElement('div')
    modalWindowIcon.className = 'modal_window__icon'

    const modalWindowCloseIcon = document.createElement('div')
    modalWindowCloseIcon.className = 'window__icon__close ic-close'

    const modalWindowIconData = document.createElement('div')
    modalWindowIconData.className = 'window__icon__data'
    // modalWindowIconData.innerHTML = Data.value

    modalWindowIcon.innerHTML = modalWindowCloseIcon.outerHTML + modalWindowIconData.outerHTML
    modalWindowDataDescriptionDiv.innerHTML = textArea.outerHTML
    modalWindowDataUser.innerHTML = modalWindowDataUserAvatar.outerHTML + modalWindowDataUserName.outerHTML
    modalWindowData.innerHTML = modalWindowDataUser.outerHTML + modalWindowDataTxtName.outerHTML + modalWindowDataName.outerHTML + modalWindowDataDescriptionTxt.outerHTML + modalWindowDataDescriptionDiv.outerHTML + button.outerHTML
    modalWindow.innerHTML = modalWindowData.outerHTML + modalWindowIcon.outerHTML
    modalBack.innerHTML = modalWindow.outerHTML

    return modalBack
  }

  this.render = function () {
    const userAvatar = this.card.querySelector('.card-user__avatar')
    const userName = this.card.querySelector('.card-user__name')
    const title = this.card.querySelector('.card-title')
    const description = this.card.querySelector('.card-description')
    const Data = this.card.querySelector('.form-control')
    this.template = createModalBlock(title, description, userAvatar, userName, Data)
    const body = document.body
    const open = this.template
    body.insertAdjacentElement('beforebegin', open)
    // Сохранение описания-------------------------------------------------------------------------------------
    const saveModW = document.querySelector('.window__data__save-description')
    saveModW.addEventListener('click', () => {
      const newNameCard = document.querySelector('.window__data__name').value
      title.innerHTML = newNameCard
      const containerTextarea = document.querySelector('.description')
      const newDescription = this.card.querySelector('.card-description')
      newDescription.classList.add('new-card__description')
      newDescription.innerHTML = containerTextarea.value
      document.querySelector('.modal_back').remove()
    })
    //    Крестик
    const closeModW = document.querySelector('.window__icon__close')
    closeModW.addEventListener('click', () => {
      document.querySelector('.modal_back').remove()
    })
  }
}

