'use strict'

import styles from './Card.css'

function Template () {
  const card = document.createElement('div')
  card.className = 'card'
  card.setAttribute('id', 'card_drop')
  card.setAttribute('draggable', true)
  card.innerHTML = `
  
      <div class="card__left-part">
      <div class="card-user"></div>
        <div class=" card-title invisible"></div>
        <div class="card-description"></div>
        <div class="card__ctn-data"></div>
        <div class="delete-card"></div>    
        <div class="add-new-card">
        <input type="text" class="card-input" placeholder="Ввести заголовок карточки" >
        <div class="btns"></div>
        <div class="card-btn-add">Добавить карточку</div>
        <div class="card-btn-close">Закрыть</div>
      </div>
    </div>
      
    
    <div class="card__right-part"></div>
    `
  return card
}
export function Card () {
  this.template = Template()
  const btnAdd = this.template.querySelector('.card-btn-add')
  this.render = function (container = '.card-composer') {
    const cont = document.querySelector(container)
    cont.append(this.template)
    this.addData = () => {
      const titleText = this.template.querySelector('.card-input').value
      const title = this.template.querySelector('.card-title')
      const cardRightPart = this.template.querySelector('.card__right-part')
      const cardUser = this.template.querySelector('.card-user')
      const addUser = `
        <div class="card-user__avatar">Фото халка</div>
        <div class="card-user__name">Халк</div>
      `
      cardUser.innerHTML = addUser
      const cardData = this.template.querySelector('.card__ctn-data')
      const addData = `
      <div class="card-data"><input type="date" id="inputDate" class="form-control"></div>
      `

      cardData.innerHTML = addData
      title.innerHTML = titleText
      cardRightPart.innerHTML = `
      <div class="card_icon">
        <div class="delite-card">&times</div>
      </div>
      <div class="transfer-card">&#10132</div>
      <div class="back-card">&#10132</div>
      `

      title.classList.remove('invisible')
      const removeCardaddStructure = this.template.querySelector('.add-new-card')
      removeCardaddStructure.remove()
    }
    const btnClose = this.template.querySelector('.card-btn-close')
    this.closeInput = () => {
      const removeCardaddStructure = this.template.querySelector('.add-new-card')
      removeCardaddStructure.remove()
    }
    btnAdd.onclick = () => {
      const inputTitle = document.querySelector('.card-input')
      const titleText = document.querySelector('.card-input').value
      if (titleText === '' || titleText === ' ') {
        inputTitle.classList.add('invalid')
      } else {
        this.addData()
      }
    }
    btnClose.onclick = this.closeInput
  }
}
