
function Template () {
  const modalProgressBuild = document.createElement('div')
  modalProgressBuild.className = 'modal'
  modalProgressBuild.innerHTML = `
  
  
  <span class="close">&times;</span>
  <p>Вы действительно хотите удалить карточку?</pn>
  <div class="modal__buttons">
  
      <button class="delete modal__button">Удалить</button>
      <button class="close-modal modal__button">Закрыть</button>
  </div>
  
  
  `
  return modalProgressBuild
}
export function ModalProgres () {
  this.template = Template()
  this.render = function () {
    const body = document.body
    body.append(this.template)
  }
}
