function Template () {
  const modalProgressAllBuild = document.createElement('div')
  modalProgressAllBuild.className = 'modal'
  modalProgressAllBuild.innerHTML = `
    
    
    <span class="close">&times;</span>
    <p>Вы действительно хотите очистить In Progress?</p>
    <div class="modal__buttons">
    
        <button class="deleteAll modal__button">Удалить</button>
        <button class="close-modal modal__button">Закрыть</button>
    </div>
    
    
    `
  return modalProgressAllBuild
}
export function ModalProgresAll () {
  this.template = Template()
  this.render = function () {
    const body = document.body
    body.append(this.template)
  }
}
