'use strict'

export function Transfer () {
  this.renderNext = function () {
    const _card = event.target.parentNode.parentNode
    const _fatherCard = event.target.parentNode.parentNode.parentNode.parentNode.parentNode
    const _nextCardDrop = _fatherCard.nextElementSibling.querySelector('.card-composer')
    if (_fatherCard.classList.contains('to_do') && _nextCardDrop.childElementCount > 6) {
      alert('Максимум ты можешь выполнять 5 задач. Либо перенеси задачу в To Do либо поднажми и выполни задачу')
    } else {
      _card.remove()
      _nextCardDrop.append(_card)
    }
  }
  this.renderBack = function () {
    // const _card = event.target.parentNode.parentNode
    // const _fatherCard = event.target.parentNode.parentNode.parentNode.parentNode.parentNode
    // const _previousCardDrop = _fatherCard.previousElementSibling.querySelector('.card-composer')
    const _card = event.target.parentNode.parentNode
    const _fatherCard = event.target.parentNode.parentNode.parentNode.parentNode.parentNode
    const _previousCardDrop = _fatherCard.previousElementSibling.querySelector('.card-composer')
    if (_fatherCard.classList.contains('done') && _previousCardDrop.childElementCount > 6) {
      alert('Максимум ты можешь выполнять 5 задач. Либо перенеси задачу в To Do либо поднажми и выполни задачу. В крайнем случае не надо было перемещать в Done задачу, которую не до конца сделал.')
    } else {
      _card.remove()
      _previousCardDrop.append(_card)
    }
  }
}
